@use 'base/variables' as v;
@use 'base/mixins' as m;

.footerbar{
    background-color: v.$bluePrusian;
    padding: 3rem;
    margin: 0;
    @include m.tablet {
        height: 100%;
    }
    h4,li, a, p{
        color: v.$white;
    }
    a, p, li{
        font-size: 1.6rem;
    }
    h4{
        text-align: center;
        margin: 0 0 3rem;
        text-transform: uppercase;
    }

    .footer-container{
        @include m.tablet {
            display: flex;
            justify-content: space-between;
        }

        .footer-links{
            text-align: center;
            @include m.tablet {
                text-align: start;                
            }

            li{
                margin: .1rem 0;
            }
        }
        .footer-contact{
            margin-bottom: 2rem;
            @include m.tablet {
                margin-bottom: 0;
                
            }
            h4{                
                @include m.tablet {
                    margin-right: 5rem;
                    
                }
            }
            .info_contact{
                display: grid;
                grid-template-columns: 1fr 2fr;
                gap: 0;
            }
            p{
                align-content: center;
                margin: 0;
                max-width: 20rem;
                border-right: solid 0.2rem v.$lightGrey;
                text-align: end;
                padding-right: 1rem;
                padding-top: 1rem;
                padding-bottom: 1rem;
                line-height: 1.2;
                
                &:first-child, &:nth-child(2){
                    padding-top: 0;

                }

                &:nth-child(5), &:nth-child(6){
                    padding-bottom: 0;

                }

                &:nth-child(even){
                    padding-left: 1rem;
                    padding-right: 0;
                    border-right: none;
                    text-align: start;
                }
            }
        }
        .footer-socialMedia{
            text-align: center;
            @include m.tablet {
                text-align: end;
                
            }

            li{
                margin: .1rem 0;
            }
        }

        .footer-links, .footer-contact, .footer-socialMedia{
            padding: 2rem;
        }

        .links-list{
            list-style: none;
            padding-left: 0;
        }
    }

    .politics{
        p{margin: 0;}
        a{align-content: center;}
        margin: 5rem 0 2rem;            
        
        .laws{
            display: flex;
            justify-content: center;
            gap: 1rem;
            margin: 1rem 0;
            @include m.tablet {
                margin: 0;
            }
            p{
                align-content: center;
            }
        }
        .copyright{
            text-align: center;
        }
    }

}