@use 'base/variables' as v;
@use 'base/mixins' as m;

.project{
    .back_button{
        @include m.button (v.$orange, v.$white);
    }
    padding: 5rem 1rem;
    h1{
        text-align: center;
        margin: 0 0 5rem;
        color: v.$darkGrey;
    }
    h4{
        color: v.$grey;
        text-align: center;
        padding: 0 1rem;
    }
    .project_info{
        .projectP{
            display: flex;
            .projectP_label{
                margin: .2rem;
                padding: .5rem;
                background-color: v.$greenBrand;
                color: v.$lightGrey;
                text-align: start;
                min-width: 9.5rem;
                font-weight: v.$bold;
                @include m.tablet {
                    min-width: 15rem;                    
                }
            }
            .projectP_info{
                margin: .2rem;
                padding: .5rem;
                background-color: v.$lightGrey;
                color: v.$grey;
                text-align: start;
                width: 100%;
                text-transform: capitalize;
            }
        }
    }
    .project_photos{
        .photos{
            display: flex;
            justify-content: center;
            img{
                min-width: 100%;
                max-width: 100%;
                margin: 1rem 0;

                @include m.tablet {
                    min-width: 80%;
                    max-width: 80%;
                }
            }
        }
    }
}