@use 'base/variables' as v;
@use 'base/mixins' as m;


h3{
    text-transform: uppercase;
    margin-bottom: 5rem;
}
.section-projects{

    background-image: linear-gradient(rgb(0, 0, 0), rgba(32, 32, 32, 0.7)),
    url(../../build/img/cover.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
}
.section-us{
    background-color: v.$white;
    overflow: hidden;
    .no_home_view{
        display: none;
    }
}

.section-servicios{
    background-color: lighten(v.$orangeFlame, 7%);
}

.section-clients{
    background-color: v.$white;
}

.section-certifications{
    background-color: lighten(v.$lightGrey, 5%);
}
.div-container{
    padding: 5rem 1rem 8rem;

    .section-title{
        color: v.$darkGrey;

        &.white{
            color: v.$white;
        }
    }
}
.noLR_padding{
    padding: 5rem 0 8rem;
}
.button-div{
    margin-top: 5rem;
    @include m.tablet {
        display: flex;
        justify-content: flex-end;
    }
    .button {
        @include m.button(lighten(v.$yellow, 0%), v.$white);
        box-shadow: 0rem .6rem 1.5rem -.5rem v.$darkGrey;
    }
}
.projects_div {
    display: grid;
    @include m.grid(2, .4rem);

    @include m.tablet {
        @include m.grid(3, .4rem);
    }

    .projects_links {
        position: relative;
        height: 18rem;
        @include m.tablet {
            height: 30rem;
        }
        width: 100%;
        border-radius: 1rem;
        overflow: hidden;
        border: solid .4rem darken(v.$lightGrey, 10%);

        p {
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            color: lighten(v.$white, 0%);
            background-color: rgba(255, 253, 253, 0.35);
            padding: 1rem;
            border-radius: .2rem;
            z-index: 2;
        }

        img {
            width: 100%;
            height: 100%;
            transition: transform 0.5s ease;
            display: block;
        }

        &:hover img {
            transform: scale(1.3);
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.5);
            opacity: .7;
            transition: opacity 0.3s ease;
            z-index: 1;
        }

        &:hover::before {
            opacity: 0;
        }

        @include m.tablet {
            &.only-mobile-visibility {
                display: none;
            }
        }
    }
}
    

.us_div{
    .us-short-description{
        color: v.$darkGrey;
        margin: 2rem 0;
        
        @include m.tablet {
            margin: 4rem 0;
            
        }

        font-weight: v.$light;
    }
}
.section-servicios{
    .services_ul{
        padding: 1rem;
        display: grid;
        margin: 0;
        @include m.grid(1, 1rem);
        
        @include m.tablet {@include m.grid(3, 2rem 1rem); }
    
        li{
            list-style: none;
            font-size: 1.6rem;
            color: darken(v.$white, 0%);
            padding: 1rem;
            box-shadow: .2rem .2rem .3rem v.$orangeFlame;
            margin: 1rem 0;
            min-height: 4.6rem;
            border-radius: .5rem;
    
            @include m.tablet {
                font-size: 2rem;
                margin: 1rem;
            }
        }
    }
}

.clients{
    display: grid;
    @include m.grid(2, 1rem);
    @include m.tablet {
        @include m.grid(5, 1rem);
    };
        text-align: center;
        align-items: center;
        p, img{
            min-height: 100%;
        }
        p{
            color: lighten(v.$bluePrusian, 7%);
            font-family: v.$font_headings;
            font-weight: v.$bold;
            text-transform: uppercase;
            text-shadow: 0 1.2rem .8rem v.$grey;
            margin: 0;
            align-content: center;
        }
}

    .certifications{
        display: grid;
        justify-items: center;
        @include m.grid(2, 1rem);
        @include m.tablet {
            @include m.grid(3, 1rem);
            
        }

            text-align: center;
            align-items: center;
            p{
                color: v.$white;
            }
    }