@use 'base/variables' as v;
@use 'base/mixins' as m;
.cta{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    margin: 3rem 0 5rem;
    @include m.tablet {
        margin: 8rem 0;
        padding: 0 18rem;
    }

    p{
        text-align: center;
        font-size: 1.8rem;
        font-weight: v.$bold;
        box-shadow: 0 0 1rem  v.$darkGrey;
        border-radius: 1rem;
        padding: .5rem;
        @include m.tablet {
            padding: 1rem;
            font-size: 3rem
        }
    }
    .cta_button{
        @include m.button (v.$greenLight, v.$white);
        margin: 0 auto;
        box-shadow: 0 .4rem 1rem 0 lighten(v.$darkGrey, 5%);
        @include m.tablet {
            width: 35rem;

        }
    }
}