@charset "UTF-8";
/** Media Queries **/
.access {
  padding: 3rem;
  background-color: rgba(246, 247, 248, 0.9725490196);
  height: 100vh;
}
.access h4 {
  text-align: center;
  margin-bottom: 6rem;
  color: #383D3B;
  text-transform: uppercase;
}
.access label {
  color: #383D3B;
}
.access .login-btn {
  background-color: #709317;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
  box-shadow: 0rem 0.6rem 1.5rem -0.6rem #383D3B;
  margin: 6rem auto;
}
@media (min-width: 768px) {
  .access .login-btn {
    width: 30rem;
    font-size: 2rem;
  }
}
.access .login-btn:hover {
  background-color: #4e6710;
  cursor: pointer;
}
.access .descripcion-pagina {
  margin-top: 10rem;
  text-align: center;
  font-size: 2.4rem;
}
.access .enlace-login {
  display: flex;
  color: #709317;
  justify-content: center;
  font-size: 1.7rem;
}

.panel {
  display: flex;
}
.panel .panel_dashboard {
  max-width: 90%;
  padding: 2rem 1rem;
  margin: 0 auto;
  overflow: scroll;
}
@media (min-width: 768px) {
  .panel .panel_dashboard {
    min-width: 50%;
  }
}
.panel .panel_dashboard h2 {
  text-align: center;
  color: #383D3B;
}
.panel .panel_dashboard hr {
  margin: 5rem 0;
}
.panel .panel_dashboard .projects_list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.panel .panel_dashboard .projects_list .project {
  margin: 3rem 0;
  background-color: rgba(243, 244, 246, 0.9725490196);
  padding: 1rem;
  border-radius: 1rem;
}
.panel .panel_dashboard .projects_list .project .project_div {
  display: flex;
  gap: 0.5rem;
  justify-content: space-between;
  margin: 0.5rem 0;
  align-items: center;
  align-content: center;
  height: 4rem;
}
.panel .panel_dashboard .projects_list .project .project_div .headers_div {
  align-content: center;
  height: 4rem;
  background-color: #FB8500;
  min-width: 10rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .projects_list .project .project_div .headers_div {
    min-width: 14rem;
  }
}
.panel .panel_dashboard .projects_list .project .project_div .headers_div p {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}
.panel .panel_dashboard .projects_list .project .project_div .content_div {
  align-content: center;
  height: 4rem;
  flex: auto;
  min-width: 6rem;
  padding: 0 0.2rem;
  overflow-x: auto;
}
.panel .panel_dashboard .projects_list .project .project_div .content_div p {
  text-wrap: nowrap;
  color: #383D3B;
  margin: 0;
}
.panel .panel_dashboard .projects_list .project .project_actions {
  height: 4rem;
  border-top: solid 0.3rem rgba(229, 231, 235, 0.9725490196);
  display: flex;
  padding-top: 0.4rem;
  justify-content: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .projects_list .project .project_actions {
    gap: 5rem;
  }
}
.panel .panel_dashboard .projects_list .project .project_actions .button {
  background-color: #f6a823;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .projects_list .project .project_actions .button {
    width: 30rem;
    font-size: 2rem;
  }
}
.panel .panel_dashboard .projects_list .project .project_actions .button:hover {
  background-color: #dd8e09;
  cursor: pointer;
}
.panel .panel_dashboard .projects_list .project .project_actions .delete {
  background-color: #E4572E;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .projects_list .project .project_actions .delete {
    width: 30rem;
    font-size: 2rem;
  }
}
.panel .panel_dashboard .projects_list .project .project_actions .delete:hover {
  background-color: #c5401a;
  cursor: pointer;
}
.panel .panel_dashboard .projects_list .project .project_actions .button, .panel .panel_dashboard .projects_list .project .project_actions .delete {
  width: 14rem;
  font-size: 1.2rem;
  padding: 0;
  align-content: center;
  height: 3.6rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .projects_list .project .project_actions .button, .panel .panel_dashboard .projects_list .project .project_actions .delete {
    width: 20rem;
    font-size: 1.6rem;
  }
}
.panel .panel_dashboard .team {
  background-color: rgba(243, 244, 246, 0.9725490196);
  border-radius: 1.5rem;
}
.panel .panel_dashboard .team .invite_text {
  margin-top: 1rem;
  padding: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .team .invite_text {
    padding: 2rem;
  }
}
.panel .panel_dashboard .team .invite_text h4 {
  border-top: 0.3rem solid rgba(229, 231, 235, 0.9725490196);
  padding-top: 3rem;
  color: #6b7280;
}
.panel .panel_dashboard .team .form {
  margin: 0 1rem;
}
.panel .panel_dashboard .team .form .button {
  margin: 4rem auto;
}
.panel .panel_dashboard .linkP {
  display: block;
  color: #0573aa;
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 3rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .linkP {
    font-size: 1.8rem;
  }
}
.panel .panel_dashboard .linkP:hover {
  color: #034160;
}
.panel .panel_dashboard .linkP.right {
  display: flex;
  justify-content: flex-end;
  margin-top: 3rem;
  margin-bottom: 0;
}
.panel .panel_dashboard .save_project {
  background-color: #F59E0B;
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
  margin-top: 6rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .save_project {
    width: 30rem;
    font-size: 2rem;
  }
}
.panel .panel_dashboard .save_project:hover {
  background-color: #c57f08;
  cursor: pointer;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .save_project {
    margin: 6rem auto;
  }
}
.panel .panel_dashboard .existing-photos {
  display: grid;
  width: 31rem;
  margin: auto;
  display: grid;
  gap: 0.2rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (min-width: 768px) {
  .panel .panel_dashboard .existing-photos {
    width: 62rem;
  }
}
.panel .panel_dashboard .existing-photos img {
  margin: 0 auto;
  width: 10rem;
  height: 10rem;
}
@media (min-width: 768px) {
  .panel .panel_dashboard .existing-photos {
    display: grid;
    gap: 0.2rem;
    grid-template-columns: repeat(6, 1fr);
  }
}
.panel .panel_dashboard .photos .photo_rmv_btn {
  display: block;
  background-color: #d52222;
  border: none;
  color: white;
  font-weight: 700;
  border-radius: 0.3rem;
  padding: 0.4rem 0.7rem;
  position: relative;
  bottom: 10rem;
  left: 7.4rem;
  box-shadow: 0 0 0.5rem rgba(229, 231, 235, 0.9725490196);
  font-size: 1.2rem;
  transition: transform 0.3s ease;
}
.panel .panel_dashboard .photos .photo_rmv_btn:hover {
  cursor: pointer;
  background-color: #e24949;
  transform: scale(1.15);
}

.sidebar_background {
  background-color: #023047;
  min-width: 6rem;
  transition: all 0.3s ease;
  min-height: 100vh;
}
.sidebar_background .sidebar {
  background-color: #023047;
  height: 100%;
  width: 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 3rem 0.2rem;
  position: fixed;
  transition: all 0.3s ease;
}
.sidebar_background .sidebar .upper_div {
  display: flex;
  flex-direction: column;
  gap: 8rem;
}
.sidebar_background .sidebar .up_bar {
  height: 7rem;
  align-content: center;
}
.sidebar_background .sidebar .up_bar .logo {
  display: none;
}
.sidebar_background .sidebar .up_bar .bar_action_icon {
  width: 6rem;
}
.sidebar_background .sidebar .up_bar .expand_icon {
  display: block;
  margin: 0 auto;
}
.sidebar_background .sidebar .up_bar .collapse_icon {
  display: none;
}
.sidebar_background .sidebar .under_div {
  align-content: center;
}
.sidebar_background .sidebar .under_div p {
  display: none;
}
.sidebar_background .sidebar .under_div .button {
  display: none;
}
.sidebar_background .sidebar .under_div .logout_icon {
  display: block;
  width: 6rem;
  margin: 0 auto;
}
.sidebar_background .sidebar .nav_bar {
  display: flex;
  flex-direction: column;
  padding: 0.3rem;
}
.sidebar_background .sidebar .nav_bar .bar_link {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.2rem 0;
  height: 5rem;
}
.sidebar_background .sidebar .nav_bar .bar_link .link_icon {
  width: 5rem;
}
.sidebar_background .sidebar .nav_bar .bar_link .link_text {
  visibility: hidden;
  transition: visibility 0.1s linear;
}
.sidebar_background.expand {
  background-color: #023047;
}
@media (min-width: 768px) {
  .sidebar_background.expand {
    min-width: 35rem;
  }
}
.sidebar_background.expand .sidebar {
  width: 90%;
  padding: 3rem 1rem;
}
@media (min-width: 768px) {
  .sidebar_background.expand .sidebar {
    width: 35rem;
  }
}
.sidebar_background.expand .sidebar .upper_div {
  justify-content: space-between;
}
.sidebar_background.expand .sidebar .up_bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar_background.expand .sidebar .up_bar .logo {
  color: #FFFFFF;
  display: block;
}
.sidebar_background.expand .sidebar .up_bar .logo h1 {
  margin: 0;
  font-size: 5rem;
}
.sidebar_background.expand .sidebar .up_bar .expand_icon {
  display: none;
}
.sidebar_background.expand .sidebar .up_bar .collapse_icon {
  display: block;
}
.sidebar_background.expand .sidebar .nav_bar {
  align-items: flex-start;
}
.sidebar_background.expand .sidebar .nav_bar .bar_link .link_text {
  visibility: visible;
  margin: 0;
  color: #FFFFFF;
  font-size: 2.4rem;
}
.sidebar_background.expand .sidebar p {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
}
.sidebar_background.expand .sidebar p span {
  color: #F59E0B;
  font-weight: 700;
}
.sidebar_background.expand .sidebar .button {
  background-color: rgb(222, 51, 51);
  padding: 1.5rem 3rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 400;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  letter-spacing: 0.1rem;
  max-width: 100%;
}
@media (min-width: 768px) {
  .sidebar_background.expand .sidebar .button {
    width: 30rem;
    font-size: 2rem;
  }
}
.sidebar_background.expand .sidebar .button:hover {
  background-color: #bf1f1f;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar_background.expand .sidebar .button {
    margin: 0 auto;
  }
}
.sidebar_background.expand .sidebar .logout_icon {
  display: none;
}

