html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 35rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-sm {
    max-width: 50rem;
  }
}
@media (min-width: 1025px) {
  .container-sm {
    max-width: 90rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 7.2rem;
  }
}

h2 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 5rem;
  }
}

h3 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 4rem;
  }
}

h4 {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 3rem;
  }
}

p {
  font-size: 1.4rem;
  color: #6b7280;
  line-height: 1.8;
}
@media (min-width: 768px) {
  p {
    font-size: 2rem;
  }
}

