@use 'base/variables' as v;
@use 'base/mixins' as m;
.dark{
    h1, h4{
        color: v.$white;
    }
    p{
        color: darken(v.$lightGrey, 5%);
    }
    background-color: darken(v.$darkBlueGrey, 5%);
    .header{
        .big-screen-topBar{
            @include m.tablet {
                background-color: darken(v.$darkBlueGrey, 15%);
            }
        }
        .mobile_bar{
            .up{
                background-color: darken(v.$darkBlueGrey, 15%);
            }
            .mobile_bar_expanded {
                background-color: darken(v.$darkBlueGrey, 15%);
            }    
        }
    }
    .section-projects{
        background-image: linear-gradient(rgb(0, 0, 0), rgba(11, 11, 11, 0.8)),
        url(../../build/img/cover.jpeg);
    }
    .section-us{
        background-color: darken(v.$darkBlueGrey, 5%);
    }
    .section-servicios{
        background-color: darken(v.$darkBlueGrey, 10%);
    }
    .section-clients{

        background-color: darken(v.$darkBlueGrey, 5%);
    }
    .section-certifications{
        background-color: darken(v.$darkBlueGrey, 10%);
    }
    .div-container{    
        .section-title{    
            color: darken(v.$white, 0%);
        }
    }
    .button-div{
        .button {   
            @include m.button(darken(v.$darkGrey, 10%), v.$white);
            box-shadow: 0rem 0rem 1.5rem -.1rem v.$white,;
    
        }
    }
    .us_div{
        .us-short-description{
                color: darken(v.$lightGrey, 5%);
        }
    }
    .section-servicios{
        .services_ul{    
            li{
                    color: darken(v.$lightGrey, 5%);
                    box-shadow: .2rem .2rem .3rem lighten(v.$darkBlueGrey, 5%);
            }
        }
    }
    .clients{
        p{
                color: darken(v.$lightGrey, 5%);
                text-shadow: none;
                box-shadow: 0rem 0rem 1.5rem -.1rem v.$white,;
        }
        img{
                box-shadow: 0rem 0rem 1.5rem -.1rem v.$white,;
        }
    }
    .us{
        h1{
                color: darken(v.$white, 0%);
        }
        .container{
            .white{
                    color: darken(v.$lightGrey, 10%);
            }
        }
        .split{
            @include m.tablet {
                display: flex;
            }
            .empresa{
                h3{
                    display: none;
                    @include m.tablet {
                            color: darken(v.$white, 0%);
                    }
                }
                h4{
                        color: lighten(v.$lightGrey, 10%);
                }
            }
        }
    }
    .projects{
        h1{
                color: darken(v.$white, 0%);
        }
        p{
                color: darken(v.$lightGrey, 0%);
        }
    }
    .project{
        .back_button{
            @include m.button(darken(v.$darkGrey, 10%), v.$white);
            box-shadow: 0rem 0rem 1.5rem -.1rem v.$white,;
        }
        h1{
            color: v.$white;
        }
        h4{
            color: v.$lightGrey;
        }
        .project_info{
            .projectP{
                .projectP_label{
                    background-color: darken(v.$darkBlueGrey, 0%);
                    color: v.$white;
                }
                .projectP_info{
                    background-color: v.$darkGrey;
                    color: v.$lightGrey;
                }
            }
        }
    }
    .services{
        h1{
                color: darken(v.$white, 0%);
        }
        .services_column{
            .services_ul{
                li{
                        color: darken(v.$lightGrey, 10%);
                        border-right: solid 0.2rem v.$grey;
                    h4{
                            box-shadow: 1.5rem 1.5rem 2rem darken(v.$darkBlueGrey, 15%);
                    }
                    &:nth-child(even){
                        h4{
                                box-shadow: -1.5rem 1.5rem 2rem darken(v.$darkBlueGrey, 15%);                    
                        }
                    }
    
                }
            }
        }
    }
    .contact{
        h1{
                color: darken(v.$white, 0%);
        }
        p{
            color: darken(v.$lightGrey, 5%);
        }
    }
    .form {
        .field {
            label {
                    color: darken(v.$white, 0%);
            }
            input, select, textarea {
                    background-color: v.$darkGrey;        
                    border: solid 0.2rem lighten(v.$darkBlueGrey, 5%);
                    &:focus{
                        background-color: darken(v.$darkBlueGrey, 10%); 
                    }
            }    
            select{
                    background-color: v.$darkBlueGrey;
            }
            input::placeholder, textarea::placeholder, select::placeholder {
                color: darken(v.$lightGrey, 10%);
            }
            input:not(:placeholder-shown), textarea:not(:placeholder-shown), select:not(:placeholder-shown) {
                color: darken(v.$lightGrey, 0%);
            }
            input[value]{
                color: darken(v.$lightGrey, 10%);
            }
        }
        select option:checked{
                    background-color: lighten(v.$darkGrey, 20%);
        }
    }
    .footerbar{
            background-color: darken(v.$darkBlueGrey, 15%);
    }
    .access {
            background-color: darken(v.$darkBlueGrey, 5%);

        h4{
            color: darken(v.$white, 0%);
        }
    }
    .cta{
        p{
            color: v.$white;
            box-shadow: 0 0 1rem  v.$lightGrey;
        }
        .cta_button{
            @include m.button (v.$yellow, v.$white);
            box-shadow: 0 .4rem 1rem 0 lighten(v.$lightGrey, 0%);
            @include m.tablet {
                width: 35rem;
    
            }
        }
    }

    .panel{
            background-color: darken(v.$darkBlueGrey, 5%);
        .panel_dashboard{
            h2{
                    color: darken(v.$white, 0%);
            }
            .projects_list{
                .project{
                        background-color: darken(v.$darkBlueGrey, 10%);
                    .project_div{
                        .headers_div{
                                background-color: darken(v.$darkBlueGrey, 0%);
                        }
                        .content_div {
                            p{
                                    color: darken(v.$lightGrey, 5%);
                            }
                        }
                    }
                }
            }
            .team{
                @media(prefers-color-scheme: dark) {
                    background-color: darken(v.$darkBlueGrey, 10%);
                }
                th, td, h4{
                        color: darken(v.$white, 0%);

                }
            }
            .linkP{
                    color: darken(v.$lightGrey, 5%);
                &:hover{
                    color: darken(v.$lightGrey, 45%);
                }
            }
        }
    }
.sidebar_background{
            background-color: darken(v.$darkBlueGrey, 15%);

    .sidebar{
            background-color: darken(v.$darkBlueGrey, 15%);
    }
    &.expand{
            background-color: darken(v.$darkBlueGrey, 15%);

        .sidebar{
            .button{
                    @include m.button (v.$darkGrey, v.$white);
            }
        }
    }
}
}