@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    .big-screen-topBar{
        display: none;

        @include m.tablet {
        display: flex;
        margin: 0;
        width: 100%;
        height: 18rem;
        padding: 2rem 5rem;
        background-color: v.$bluePrusian;

        justify-content: space-between;
        align-items: center;
        }
        .left{
            display: block;
            h1{
                margin: 0;
                color: v.$greenBrand;
            }
            p{
                margin: -1.5rem auto 0;
                font-size: 1.3rem;
                color: lighten(v.$grey, 25%);
                text-transform: uppercase;
                font-family: v.$font_headings;
                text-align: center;
                font-weight: v.$bold;
            }

        }
        .center{
            .mobile-menu{
                @include m.tablet {
                    display: none;
                };
            }
            .nav{
                display: flex;
                gap: 2rem;
                a{
                    align-content: center;
                    color: v.$white;
                    font-size: 2rem;
                    transition: transform .3s ease;

                    &:hover{
                        transform: scale(1.1);
                    }
                }

            }
        }
        .right{
            display: flex;
            flex-direction: row;
            gap: 2rem;
            img{
                width: 5rem;

                &:hover{
                    cursor: pointer;
                }
            }
        }
    }
    // Here starts the mobile bar code
    .mobile_bar{
        .up{
            height: 10rem;
            background-color: v.$bluePrusian;
            display: flex;
            margin: 0;
            width: 100%;
            padding: 2rem;
            justify-content: space-between;
            a{
                align-content: center;
                margin-left: 1rem;
                h1{
                    margin: 0;
                    color: v.$greenBrand;
                }
                p{
                    margin: -1.5rem auto 0;
                    font-size: 1.3rem;
                    color: lighten(v.$grey, 25%);
                    text-transform: uppercase;
                    font-family: v.$font_headings;
                    text-align: center;
                    font-weight: v.$bold;
                }
            }
    
            .expand_bar_btn, .collapse_bar_btn{
                width: 5rem;
                margin-right: 2rem;
            }

            .expand_bar_btn{
                display: none;
                &.show_btn{
                    display: block;
                }
            }

            .collapse_bar_btn{
                display: none;
                &.show_btn{
                    display: block;
                }
            }
        }

        @include m.tablet {
            display: none;
        };
        .mobile_bar_expanded {
            opacity: 0;
            overflow: hidden;
            height: 0;
            background-color: v.$bluePrusian;
            transition: opacity 0.3s ease;
            overflow-y: hidden; /* Agregar esta línea */
        }
        
        &.mostrar {
            opacity: 1;
            position: fixed;
            z-index: 99;
            inset: 0;
            .mobile_bar_expanded {
                height: 100%; /* Cambiar height: 100% a height: 100vh */
                opacity: 1;
                .nav {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin-top: 10rem;
                    a {
                        color: v.$white;
                        font-size: 2.4rem;
                        margin: .5rem 0;
                    }
                }
                .down{
                    margin-top: 10rem;
                    display: flex;
                    flex-direction: column;

                    align-items: center;
                    gap: 2rem;
                    img{
                        width: 7rem;
        
                        &:hover{
                            cursor: pointer;
                        }
                    }
                }
            }
        }        
    }
}