@use 'base/variables' as v;
@use 'base/mixins' as m;

.projects{
    padding: 5rem 1rem;
    @include m.tablet {
        padding: 5rem;
    }
    h1{
        text-align: center;
        margin: 0 0 5rem;
        color: v.$darkGrey;
    }
    p{
        color: v.$grey;
        text-align: center;
        padding: 0 1rem;
        @include m.tablet {
            width: 80%;
            margin: 0 auto;
            margin-bottom: 3rem;
        }
    }
    .projects_links{
        border: solid .2rem darken(v.$lightGrey, 5%);
    }
}