@use 'base/variables' as v;
@use 'base/mixins' as m;

.services{
    h1{
        text-align: center;
        margin: 5rem 0 2rem;
        color: v.$darkGrey;
        @include m.tablet {
            margin: 5rem 0;
        }
    }

    .services_column{
        padding: .5rem;
        @include m.tablet {
            padding: 3rem;
            
        }
        .services_ul{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 0 1rem;
            padding: 0;
            @include m.tablet {
                gap: 0 2rem;
            }
            li{
                padding: 1rem 2rem;
                @include m.tablet {
                    padding: 2rem 3rem;
                }
                list-style: none;
                color: v.$darkGrey;
                border-right: solid 0.2rem v.$lightGrey;
                h4{
                    font-size: 1.4rem;
                    text-align: end;
                    margin: 0 0 1rem;
                    border-bottom-right-radius: 1.5rem;
                    box-shadow: 1.5rem 1.5rem 2rem darken(v.$lightGrey, 5%);
                    @include m.tablet {
                        margin: 0 0 3rem;
                        font-size: 2.4rem;
                    }
                    padding: 0 .5rem .5rem;
                    @include m.tablet {
                        padding: 0 1rem 1rem;
                    }
                }
                p{
                    margin-bottom: 4rem;
                    @include m.tablet {
                        margin-bottom: 8rem;
                    }
                    margin-top: 0;
                }
                &:nth-child(even){
                    padding-left: .5rem;
                    @include m.tablet {
                        padding-left: 1rem;
                    }
                    border-right: none;
                    h4{
                        border-bottom-left-radius: 1.5rem;
                        box-shadow: -1.5rem 1.5rem 2rem darken(v.$lightGrey, 5%);
                        text-align: start;
                    }
                    p{
                        margin-bottom: 4rem;
                        @include m.tablet {
                            margin-bottom: 8rem;
                        }
                        margin-top: 0;
                    }
                }
            }
        }
    }
}