@use 'base/variables' as v;
@use 'base/mixins' as m;

.contact{
    padding: 5rem 1rem;
    @include m.tablet {
        padding: 5rem;
    }
    h1{
        text-align: center;
        margin: 0 0 5rem;
        color: v.$darkGrey;
    }
    p{
        text-align: center;
        margin-top: 0;
        margin-bottom: 3rem;

        @include m.tablet {
            width: 80%;
            margin: 0 auto;
            margin-bottom: 3rem;
        }
    }
    .g-recaptcha{
        div{
            margin: auto;
        }
    }
}