@use 'base/variables' as v;
@use 'base/mixins' as m;

.us{
    h1{
        text-align: center;
        margin: 5rem 0 2rem;
        color: v.$darkGrey;
        @include m.tablet {
            margin: 5rem 0;
        }
    }
    .container{

        .white{
            //unset values
            color: v.$yellow;
            text-align: center;
        }
    }
    .split{
        @include m.tablet {
            display: flex;
        }
        .empresa{
            @include m.tablet {
                flex: 0 0 60%;
            }
            padding: 2rem;
            @include m.tablet {
                padding: 3rem; 
            }
            h3{
                display: none;
                @include m.tablet {
                    display: block;
                    margin: 0;
                    padding: 2rem 2rem 3rem;
                    color: v.$darkGrey;
                }
            }
            h4{
                text-align: start;
                margin: 0;
                padding: 0 0 3rem;
                width: 100%;

            }
            .us_div{
                @include m.tablet {
                    padding: 0 2rem;
                  }
            }
        }

        .mis_vis{
            background-color: v.$grey;
            margin: 1rem;
            border-radius: 1rem;
            p, h3{
                margin: 0;
                padding: 2rem;
          }
          h3{
            color: v.$white;
          }
          p{
            color: v.$lightGrey;
          }
          @include m.tablet {
            margin: 3rem;
          }
        }
    }
}